.blog-page-container {
    width: 50%;
    margin: auto;
    padding: 6rem 0 0 0;
    min-height: 50vh;
}
.blog-page-title {
    font-size: 3rem;
    margin-bottom: 2rem;
}
.passage-component{
    margin: 0 0 1rem 0;
}
.image-component{
    /* width: 100%; */
    height: 40vh;
    display: block;
    margin: 0 0 1rem 0;
}
.image-component > img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.subheading-component {
    margin: 0 0 1rem 0;
}
.comments-section {
    font-size: 1.2rem;
}
.comment-container {

}
.comment {
    padding: 1rem 1rem;
    border: 2px solid rgba(128, 128, 128, 0.635);
}
.comment> p{
    font-size: 1.4rem;
    font-weight: 600;
    margin-bottom: .6rem;
}
.comment-author {
    padding: 2px;
    display: flex;
    align-items: center;
    gap: .5rem
}
.comment-author >div>p {
    color: rgb(63, 63, 63);
    font-size: 1rem;
}
.comment-author > img {
    border-radius: 50%;
    display: block;
    width: 2rem;
    height: 2rem;
    padding: 2px;
    border: 2px solid black;
}
.subcomment-author {
    margin: 1rem 0 1rem 5%;
    font-size: 1.4rem;
    font-weight: 600;
}
.subcomment> p{
    
    margin-bottom: .6rem;
}
.subcomment>div>img {
    border-radius: 50%;
    display: block;
    width: 2rem;
    height: 2rem;
    padding: 2px;
    border: 2px solid black;
}
.subcomment>div {
    padding: 2px;
    display: flex;
    align-items: center;
    gap: .5rem
}
.subcomment>div>div>p {
    color: rgb(63, 63, 63);
    font-size: 1rem;
    font-weight: 400;
}
.subcomment-author> button {
    background-color: white;
    outline: none;
    border: none;
    margin: .7rem 0;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 30px;
    padding: .3rem .7rem;
    position: relative;
    box-shadow: 2px 2px 10px rgba(72, 72, 72, 0.5);
    transition: 500ms;
}
.subcomment-author> button:hover {
    color: white;
    background-color: black;
}
.subcomment-author> button::before {
    position: absolute;
    content:"";
    inset: 0;
    width: 0;
    background: linear-gradient(90deg, #f8be3b, #ee3078, #8028c3);
    filter: blur(6px);
    z-index: -1;
    transition: 500ms;
}
.subcomment-author> button:hover::before {
    position: absolute;
    content:"";
    inset: 0;
    width: 100%;
    background: linear-gradient(90deg, #f8be3b, #ee3078, #8028c3);
    filter: blur(10px);
    z-index: -1;
}









