.loading-blog-error-container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 70vh;
}
.loading-blog-error {
    width: 50%;
    height: 30vh;
    border: 2px solid grey;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.2rem;
    transition: 500ms;
}
.loading-blog-error:hover {
    box-shadow: 2px 2px 15px rgba(93, 93, 93, 0.32);
}
.loading-blog-error>h1 {
    text-transform: uppercase;
    color: red;
    
}
.loading-blog-error>button {
    outline: none;
    border: none;
    cursor: pointer;
    background-color: black;
    color: white;
    font-size: 1.2rem;
    padding: .5rem .7rem;
    border-radius: 30px;
    position: relative;
}
.loading-blog-error>button::before {
    position: absolute;
    content: "";
    width: 0;
    height: 100%;
    top:0;
    left: 0;
    background: linear-gradient(90deg, #f8be3b, #ee3078, #8028c3);
    filter: blur(10px);
    z-index: -1;
    transition: 300ms;
}
.loading-blog-error>button:hover::before {
    width: 100%;
}
