.authenticate-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100dvh;
}
.authenticate {
    width: 30%;
    min-height: 50vh;
    /* background-color: green; */
    border: 2px solid rgba(128, 128, 128, 0.58);
}
.auth-container {
    text-align: center;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.input-wraper > input {
    all:unset;
    box-sizing: border-box;
    padding: .3rem;
    display: block;
    width: 100%;
    border: 2px solid rgba(128, 128, 128, 0.58);
    text-align: left;
    font-size: 1.2rem;
    margin: .7rem auto;
    transition: 500ms;
    position: relative;
    background-color: white;
}
.input-wraper > input::placeholder {
    font-family: Roboto, sans-serif;
}
.input-wraper > input:active, .input-wraper > input:focus  {
    border: 2px solid black;
}
.input-wraper{
    width: 80%;
    margin: auto;
    position: relative;
}
.input-wraper::before{
    position: absolute;
    content: "";
    width: 0;
    top:0;
    left: 0;
    height: 100%;
    z-index: -1;
    background: linear-gradient(90deg, #f8be3b, #ee3078, #8028c3);
    filter: blur(3px);
    transition: 500ms;
    opacity: .5;
}
.input-wraper:hover::before, .input-wraper:focus::before, .input-wraper:active:before, .input-wraper:focus-within:before{
    width: 100%;
}
.authenticate-inputs > button {
    all:unset;
    box-sizing: border-box;
    background-color: black;
    width: 80%;
    color: white;
    padding: .7rem .3rem;
    margin: .5rem auto;
    position: relative;
    cursor: pointer;
}
.authenticate-inputs > button::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    z-index: -1;
    height: 100%;
    background: linear-gradient(90deg, #f8be3b, #ee3078, #8028c3);
    filter: blur(10px);
    transition: 500ms;
}
.authenticate-inputs > button:hover::before, .authenticate-inputs > button:focus::before, .authenticate-inputs > button:active:before, .authenticate-inputs > button:focus-within:before{
    width: 100%;
}
.authenticat-options {
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    margin: .5rem auto;
}
.authenticat-options > hr {
    width: 100%;
    height: 2px;
}
.auth-container > button {
    all: unset;
    box-sizing: border-box;
    width: 80%;
    padding: .7rem .3rem;
    background-color: rgba(197, 197, 197, 0.605);
    margin: .5rem auto;
    cursor: pointer;

}
.error-display {
    text-align: left;
    width: 80%;
    margin: 1rem auto .5rem auto;
    font-weight: 400;
    border-left: 4px solid red;
    padding: .5rem;
    color: rgb(104, 5, 5);
    background-color: rgba(231, 145, 145, 0.696);

}