.blogs-card-container {
    width: 60%;
    margin: auto;
    z-index: 100;
    position: relative;
}
.blog-cards {
    width: 100%;
    padding-bottom: 1.5rem;
    margin: 1.5rem 0 0 0;
    border-bottom: 2px solid #00000070;
    display: flex;
    justify-content: space-around;
    position: relative;
}



.blog-card-text-side {
    max-width: 60%;
    text-align: left;
}

.blog-card-image-side {
    width: 30%;
    padding: 1rem;
}

.blog-card-image-side>img {
    width: 100%;
    object-fit: cover;
    background-size: cover;
    object-position: center;
    display: block;
}

.blog-card-text-side>h1 {
    width: 100%;
    margin-bottom: .7rem;
    text-wrap: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 2.5rem;
}

.user-tablet {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: .7rem;
    transition: 500ms;
    width: fit-content;
    padding-right: 2rem;
    border-radius: 20px;
}

.user-tablet>img {
    height: 40px;
    width: 40px;
    display: block;
    border-radius: 50%;
    padding: 2px;
    border: 3px solid black;
    box-shadow: 0 0  12px rgba(114, 114, 239, 0.674);
}

.user-tablet>div>h4 {
    font-weight: 400;
    font-size: 1.2rem;
}

.user-tablet>div>p {
    color: rgba(0, 0, 0, 0.553);
    font-size: .9rem;
}

.tags-display {
    font-size: 1rem;
    display: inline-block;
    font-weight: 300;
    color: #fff;
    font-size: .9rem;
    background-color: #000;
    padding: .3rem .5rem;
    border-radius: 30px;
    margin-right: .5rem;
    position: relative;
}

.tags-display::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 0;
    transform: translate(-10%, -10%);
    z-index: -1;
    background: linear-gradient(90deg, #f8be3b, #ee3078, #8028c3);
    filter: blur(10px);
    transition: 500ms;
}
.tags-display:hover:before {
    width: 100%;
}

.blog-card-text-side>p {
    color: #000000b6;
    margin-bottom: 1rem;
}

.blog-info {
    display: flex;
    justify-content: space-between;
}

.blog-reactions {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.blog-reactions>div>img {
    margin-right: .3rem;
    display: inline-block;
}