.hero-contianer {
    height: 70vh;
    width: 100%;
    position: relative;
}
.hero-image {
    height: 100%;
}
.hero-image > img  {
    height: 100%;
    width: 100%;
    display: block;
    background-image: cover;
    object-position:center;
    object-fit: cover;
}
.hero-text {
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    width: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    z-index: 10000;
}
.hero-text > h2 {
    user-select: none;
    text-shadow: 2px 2px 10px #000;
    font-size: 7rem;
}
.searchbar-container {
    width: 40%;
    margin: auto;
    display: flex;
    align-items: center;
    gap: 1rem;
    justify-content: space-between;
    padding: .4rem 1.5rem;
    border-radius: 50px;
    background-image: linear-gradient(90deg, rgba(255, 255, 255, 0.838) 40%, rgba(255, 255, 255, 0.546), rgba(255, 255, 255, 0.838));
    box-shadow: 2px 2px 10px #000000a8;
}
.searchbar-container > input {
    all: unset;
    cursor: text;
    width: 100%;
    text-align: left;
    color: black;
    font-size: 1.4rem;
}
.searchbar-container > input::placeholder {
    font-size: 1.4rem;
    color: rgba(0, 0, 0, 0.655);
}
.searchbar-container > img {
    width: 1.8rem;
    cursor: pointer;
}