.footer-container {
    background-color: rgba(195, 194, 194, 0.242);
}
footer {
    min-height: 40vh;
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.topper-footer {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
.topper-quicklinks>h3, .topper-themelinks>h3, .the-developer>h3 {
    font-weight: 400;
    padding: .5rem 0;
}