nav {
    position: fixed;
    top:0;
    left:0;
    right: 0;
    background-color: transparent;
    box-shadow: 2px 2px 2px #00000057;
    background-color: white;
    z-index: 99999999;
}
.navbar-container {
    background-color: transparent;
    width: 80%;
    margin: .6rem auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.logo-display {
    font-weight: 900;
    font-size: 2.25rem;
    cursor: pointer;
}
.navlist-container {
    display: flex;
    list-style: none;
    align-items: center;
    gap: 2rem
}
.navlist-container > a {
    text-decoration: none;
    color: inherit;
}
.navlist-container > a > li {
    transition: 500ms;
    display: flex;
    flex-direction: column;
}
.navlist-container > a.active > li {
    color: black;
}
.navlist-container > a > li::after{
    content: "";
    width: 0%;
    margin: 0 0 0 0;
    height: 4px;
    border-radius: 10px;
    background-color: black;
    transition: 200ms;
}
.navlist-container > a.active > li::after{
    content: "";
    width: 100%;
    margin: 0 0 0 0;
    height: 4px;
    border-radius: 10px;
    background-color: black;
    transition: 500ms;
}
.navlist-container > a > li:hover::after{
    content: "";
    width: 100%;
    margin: 0 0 0 0;
    height: 4px;
    border-radius: 10px;
    background-color: black;
    transition: 500ms;
}
.user-profile-image {
    display: flex;
    align-items: center;
    gap: 1rem;
}
.user-profile-image > div {
    object-fit: cover;
    cursor: pointer;
    border-radius: 50%;
    padding: 2px;
    border: 3.5px solid black;
    position: relative;
}
.user-profile-image >div::after {
    content: "";
    position: absolute;
    height: 1rem;
    width: 1rem;
    background-image: radial-gradient( #50c878, #008080);
    bottom:0;
    right: 0;
    border-radius: 50%;
    z-index: 2;
    transform: translate(30%, 30%);
}
.user-profile-image > div > img {
    height: 2.25rem;
    width: 2.25rem;
    display: block;
    object-fit: cover;
    border-radius: 50%;
}
.user-profile-image > h2 {
    font-weight: 400;
    font-size: 1.2rem;
    border: 2px solid black;
    color: white;
    background-color: black;
    cursor: pointer;
    padding: .2rem .7rem;
    border-radius: 30px;
    transition: 500ms;
}
.user-profile-image > h2:hover {
    background-color: white;
    color: black;
}
.menuButton {
    height: 1.7rem;
    cursor: pointer;
    display: none;
}

@media only screen and (max-width: 1024px) {
    .logo-display {
        font-size: 1.8rem;
    }
    .navlist-container > a > li {
        font-size: 1.1rem;
    }
    .user-profile-image > div > img {
        height: 1.8rem;
        width: 1.8rem;
    }
    .user-profile-image > h2 {
        font-size: 1rem;
    }
}
@media only screen and (max-width: 768px)  {
    .menuButton {
        display: block;
        height: 1.4rem;
    }
    .navlist-container {
        display: none;
    }
}
